












import Vue from "vue";
import QDocumentViewer from "../QDocumentViewer.vue";
export default Vue.extend({
  components: {
    QDocumentViewer,
  },
  data() {
    return {
      show: true,
      loadedRatio: 0,
      numPages: 0,
      turn: 0,
    };
  },

  name: "VPdfFileViewer",
  props: {
    src: {
      default: "",
      type: [String, Object],
    },
    height: {
      type: String,
    },
    width: {
      type: String,
    },
    page: {
      type: Number,
      default: 1,
    },
    scale: {
      type: String,
    },
    loadingContent: {
      type: String,
      default: "Loading...",
    },
    customCompClass: {
      default: "pdf",
      type: String,
    },
  },

  destroyed() {
    this.$el?.remove();
  },
});
